@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary: #15192A;
  --secondary: #DC1600;
}

.container {
  min-height: 100vh;
  min-width: 100vw;
}

.header {
  height: 10vh;
}

.main {
  min-height: 90vh;
}

.bg-primary {
  background: var(--primary);
  color: #fff;
}

.bg-secondary {
  background: var(--secondary);
  color: #fff;
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}